import React, { useState, useRef, useEffect } from 'react'; // Import React and useState
import { CropperRef, Cropper, CircleStencil } from 'react-advanced-cropper';
import 'react-advanced-cropper/dist/style.css';
import { ref, uploadBytesResumable, getDownloadURL, deleteObject } from 'firebase/storage';
import { doc, getDoc, updateDoc, deleteField, setDoc, onSnapshot } from 'firebase/firestore';
import { storage, firestore } from '../firebase';


function ImageCropper({
  closeModal,
  photoURL,
  handleImageUpload,
  setPhotoURL,
  user,
  collectionName,
}) {
  const [image, setImage] = useState(photoURL);
  const [selectedFile, setSelectedFile] = useState(null);
  const [isDeleteConfirmed, setIsDeleteConfirmed] = useState(false);
  const [selectedFileName, setSelectedFileName] = useState(null);
  const [docRef, setDocRef] = useState(null); // Added local state for the document reference
  const cropperRef = useRef(null);
  const fileInputRef = useRef(null);
  const [isUploadSuccess, setIsUploadSuccess] = useState(false);

  const CustomCheckmark = (
  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6">
    <path strokeLinecap="round" strokeLinejoin="round" d="M4.5 12.75l6 6 9-13.5" />
  </svg>
);


  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    setSelectedFile(file); // Update the selectedFile state with the file
  
    if (file) {
      setSelectedFileName(file.name);
      const reader = new FileReader();
      reader.onload = (event) => {
        setImage(event.target.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleNewPhoto = () => {
    fileInputRef.current.click();
  };

  const handleUpload = async () => {
    const canvas = cropperRef.current?.getCanvas();
    if (canvas) {
      const blob = await new Promise((resolve) => canvas.toBlob(resolve, 'image/jpeg'));
  
      if (blob) {
        const storageRef = ref(storage, `profileImages/${selectedFileName}`);
        const metadata = { contentType: 'image/jpeg' };
  
        const uploadTask = uploadBytesResumable(storageRef, blob, metadata);
  
        uploadTask.on(
          'state_changed',
          (snapshot) => {
            // Handle upload progress here
          },
          (error) => {
            // Handle upload error here
          },
          () => {
            // Handle upload success here
            getDownloadURL(uploadTask.snapshot.ref).then(async (downloadURL) => {
              if (downloadURL) {
                // Update the Firestore document immediately before handling the upload success
                await updateDoc(docRef, { photoURL: downloadURL });
                handleImageUpload(downloadURL);
                setIsUploadSuccess(true);
                setTimeout(() => {
                  setIsUploadSuccess(false);
                }, 3000);
              }
            });
          }
        );
      }
    }
  };
  

  const handleDeletePhoto = async () => {
    if (user && user.uid) {
      const defaultImageURL = `${process.env.PUBLIC_URL}/images/arts/user.webp`;
      await updateDoc(docRef, { photoURL: defaultImageURL });
      setPhotoURL(defaultImageURL);
      setIsDeleteConfirmed(false);
    }
  };
  

  useEffect(() => {
    fileInputRef.current = document.createElement('input');
    fileInputRef.current.type = 'file';
    fileInputRef.current.accept = 'image/*';
    fileInputRef.current.addEventListener('change', handleFileUpload);

    const userDocRef = doc(firestore, collectionName, user.uid);
    setDocRef(userDocRef); // Set the local state for document reference

    const handlePhotoURLChange = (newPhotoURL) => {
      setImage(newPhotoURL);
    };
  
    const unsubscribe = onSnapshot(userDocRef, (doc) => {
      const newPhotoURL = doc.data()?.photoURL;
      handlePhotoURLChange(newPhotoURL);
    });
  
    return () => unsubscribe();
  }, [user.uid, collectionName]);

  return (
    <div className="modalimage align-items-center">
      <div className="top-section">
        <div style={{ marginRight: '10px' }}>Profile image</div>
        <button className="btn-crop" onClick={closeModal}>
    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"  stroke="white" strokeWidth="1.5"  className={`w-6 h-6 smaller-svg`}>
            <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
      </div>
      <div className='centerimage'>
      <div className="croppers-wizard__body">
        <div className='default-cropper croppers-wizard__cropper'>

       
          {image ? (
            <Cropper
              ref={cropperRef}
              src={image}
              stencilComponent={CircleStencil}
              stencilProps={{
                grid: true,
              }}
         
            />
          ) : null}
</div>

</div>

      </div>
      
      <div className="button-section with-divider">
  <button className="btn-crop" onClick={handleNewPhoto}>
    <div className="button-content">
      <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="white" strokeWidth="1.5" className={`w-6 h-6 smaller-svg`}>
        <path strokeLinecap="round" strokeLinejoin="round" d="M9.53 16.122a3 3 0 00-5.78 1.128 2.25 2.25 0 01-2.4 2.245 4.5 4.5 0 008.4-2.245c0-.399-.078-.78-.22-1.128zm0 0a15.998 15.998 0 003.388-1.62m-5.043-.025a15.994 15.994 0 011.622-3.395m3.42 3.42a15.995 15.995 0 004.764-4.648l3.876-5.814a1.151 1.151 0 00-1.597-1.597L14.146 6.32a15.996 15.996 0 00-4.649 4.763m3.42 3.42a6.776 6.776 0 00-3.42-3.42" />
      </svg>
      New Photo
    </div>
  </button>

  <button className="btn-crop" onClick={handleUpload}>
        <div className="button-content">
          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="white" strokeWidth="1.5" className={`w-6 h-6 smaller-svg`}>
            <path strokeLinecap="round" strokeLinejoin="round" d="M6.827 6.175A2.31 2.31 0 015.186 7.23c-.38.054-.757.112-1.134.175C2.999 7.58 2.25 8.507 2.25 9.574V18a2.25 2.25 0 002.25 2.25h15A2.25 2.25 0 0021.75 18V9.574c0-1.067-.75-1.994-1.802-2.169a47.865 47.865 0 00-1.134-.175 2.31 2.31 0 01-1.64-1.055l-.822-1.316a2.192 2.192 0 00-1.736-1.039 48.774 48.774 0 00-5.232 0 2.192 2.192 0 00-1.736 1.039l-.821 1.316z" />
            <path strokeLinecap="round" strokeLinejoin="round" d="M16.5 12.75a4.5 4.5 0 11-9 0 4.5 4.5 0 019 0zM18.75 10.5h.008v.008h-.008V10.5z" />
          </svg>
          Upload Photo
        </div>
      </button>

      {isUploadSuccess && (
  <div className="upload-success-icon">
    <svg xmlns="http://www.w3.org/2000/svg" color="#009E60" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor">
      <path strokeLinecap="round" strokeLinejoin="round" d="M4.5 12.75l6 6 9-13.5" />
    </svg>
  </div>
)}

 

  <button className="btn-crop" onClick={handleDeletePhoto}>
    <div className="button-content">
      <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="white" strokeWidth="1.5" className={`w-6 h-6 smaller-svg`}>
        <path strokeLinecap="round" strokeLinejoin="round" d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0" />
      </svg>
      Delete Photo
    </div>
  </button>
</div>



    </div>
  );
}

export default ImageCropper;
